.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@font-face {
  font-family: Shabnam;
  src: url('shabnam-font/dist/Shabnam.eot');
  src: url('shabnam-font/dist/Shabnam.eot?#iefix') format('embedded-opentype'),
       url('shabnam-font/dist/Shabnam.woff') format('woff'),
       url('shabnam-font/dist/Shabnam.ttf') format('truetype');
  font-weight: normal;
}
      

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.TopNav{
  background:#fff;
  margin:6px;
}
.TopNav .navbar-collapse{
  padding:0;
}
li.SiteName a{
  font-size:20px !important;
}

@media screen and (min-width:768px){
  
  .imgGroup{
    width:100%;
    margin:0 auto;
    padding-right:10%;
    padding-left:10% ;
}
.logoOtaghak{
  height: 42px;
  text-align:right;
  margin-top:8px;
  margin-right:16px;
}
}
@media screen and (max-width:835px){
.logoOtaghak{
  height: 32px;
  text-align:right;
