}
}
@font-face {
  font-family: 'iranSans';
  font-style: normal;
  font-weight: 400;
  src: url('assets/fonts/IRANSans.ttf'); /* IE9 Compat Modes */
  
}

body {
  font-family: 'iranSans';
  background:#F4F4F3;
overflow-x:hidden;
}
.TopNav .fa {
  margin-top:2px;
  font-size:13px;
  padding-left:2px;
}
.TopNav {
  font-size:12px;
  border:none;
}
.TopNav a {
  color:#222 !important;
}
.TopNav .navbar-toggle   {
    float: left;
    margin-left: 16px;
    border:none;
}
.icon-bar{
  background:#ed1459 !important;
  width:20px;
}
.TopNav .navbar-default .navbar-toggle:focus, .navbar-default .navbar-toggle:hover{
  background-color:#fff;
}
@media screen and (min-width:768px){
.boxSite .DescSite{
  position:absolute;
  top:23%;
  right:0;
  height:auto;
  background:rgba(213, 212, 212, 0.8);
  border-radius:15px 0 0 15px;
   filter:contrast(150%);
  color:#222;
}
.titleBox{
  padding:15px;
  font-size:38px;
  font-weight:bold;
  text-align:center;
  font-family:Shabnam;
  margin:0;
}
.descriptionBox{
  padding:10px;
  font-family:Shabnam;
  font-size:25px;
  text-align:center;
  direction:rtl;

}
.register button{
  box-shadow: inset 0 -1px 1px rgba(237,20,89,0.5),inset 0 1px 1px rgba(237,20,89,1); /*top internal highlight*/
 
  background: linear-gradient(to bottom,rgb(237,20,122) 10%, #ed1459 20%,#ed1459 76%,rgb(140,11,39) 100%);
  border-radius:7px;
  padding:10px 30px 10px 30px !important ;
  color:#fff;
  font-size:38px;
  font-weight:bold;
  border:none;
  font-family:Shabnam;
  width:250px;
}
.register {
    position:absolute;
    margin-top:20px;
    text-align: center;
    
}
}

